<div className="overflow-x-auto rounded-lg shadow-lg">
  <table className="min-w-full table-auto bg-white text-sm text-gray-800">
    <thead>
      <tr className="bg-gray-200 text-left font-semibold uppercase text-gray-700">
        <th className="px-6 py-3">Nome</th>
        <th className="px-6 py-3">Pontos</th>
        <th className="px-6 py-3">Saldo de Gols</th>
        <th className="px-6 py-3">Vitórias</th>
        <th className="px-6 py-3">Derrotas</th>
        <th className="px-6 py-3">Ações</th>
      </tr>
    </thead>
    <tbody>
      {jogadores.map((jogador, i) => (
        <tr
          key={jogador.id}
          className={i % 2 === 0 ? 'bg-white' : 'bg-gray-50'}
        >
          <td className="px-6 py-3">{jogador.nome}</td>
          <td className="px-6 py-3">{jogador.pontos}</td>
          <td className="px-6 py-3">{jogador.saldoGols}</td>
          <td className="px-6 py-3">{jogador.vitorias}</td>
          <td className="px-6 py-3">{jogador.derrotas}</td>
          <td className="px-6 py-3">
            <Button size="small" className="bg-blue-600 hover:bg-blue-700 text-white">
              Editar
            </Button>
          </td>
        </tr>
      ))}
    </tbody>
  </table>
</div>
