'use client';

import { useState } from 'react';
import { Jogador, Partida } from '@/app/types';
import Header from '@/app/components/Header';
import TabelaJogadores from '@/app/components/TabelaJogadores';
import TabelaPartidas from '@/app/components/TabelaPartidas';
import ModalJogador from '@/app/components/ModalJogador';
import ModalPartida from '@/app/components/ModalPartida';
import { jogadoresExemplo } from '@/app/data/jogadores';
import { partidasExemplo } from '@/app/data/partidas';

export default function Page() {
  const [jogadores, setJogadores] = useState<Jogador[]>(jogadoresExemplo);
  const [partidas, setPartidas] = useState<Partida[]>(partidasExemplo);
  const [jogadorEditando, setJogadorEditando] = useState<Jogador | null>(null);
  const [partidaEditando, setPartidaEditando] = useState<Partida | null>(null);
  const [modalJogadorAberto, setModalJogadorAberto] = useState(false);
  const [modalPartidaAberto, setModalPartidaAberto] = useState(false);

  const isAdmin = true; // Supondo que o usuário seja admin

  const abrirModalJogador = () => setModalJogadorAberto(true);
  const fecharModalJogador = () => setModalJogadorAberto(false);
  const abrirModalPartida = () => setModalPartidaAberto(true);
  const fecharModalPartida = () => setModalPartidaAberto(false);

  // Corrigir a tipagem para ser 'keyof Jogador' na função 'ordenarPor'
  const ordenarPor = (campo: keyof Jogador) => {
    const jogadoresOrdenados = [...jogadores].sort((a, b) => {
      if (a[campo] < b[campo]) return -1;
      if (a[campo] > b[campo]) return 1;
      return 0;
    });
    setJogadores(jogadoresOrdenados);
  };

  const salvarJogador = (jogador: Jogador) => {
    if (jogadorEditando) {
      setJogadores(jogadores.map((j) => (j.id === jogador.id ? jogador : j)));
    } else {
      setJogadores([...jogadores, { ...jogador, id: Date.now().toString() }]);
    }
    fecharModalJogador();
  };

  const salvarPartida = (partida: Partida) => {
    if (partidaEditando) {
      setPartidas(partidas.map((p) => (p.id === partida.id ? partida : p)));
    } else {
      setPartidas([...partidas, { ...partida, id: Date.now().toString() }]);
    }
    fecharModalPartida();
  };

  return (
    <div className="p-4">
      <Header
        ordenarPor={ordenarPor} // Passando a função correta
        isAdmin={isAdmin}
        abrirModalJogador={abrirModalJogador}
        abrirModalPartida={abrirModalPartida}
      />
      <TabelaJogadores
        jogadores={jogadores}
        onEditar={setJogadorEditando}
        isAdmin={isAdmin}
      />
      <TabelaPartidas
        partidas={partidas}
        onEditar={setPartidaEditando}
        isAdmin={isAdmin}
      />

      <ModalJogador
        isOpen={modalJogadorAberto}
        onClose={fecharModalJogador}
        onSalvar={salvarJogador}
        jogadorEditando={jogadorEditando}
      />

      <ModalPartida
        isOpen={modalPartidaAberto}
        onClose={fecharModalPartida}
        onSalvar={salvarPartida}
        partidaEditando={partidaEditando}
      />
    </div>
  );
}
