'use client';

import { Partida } from '@/app/types';
import Button from '@/app/components/ui/Button';;

type Props = {
  partidas: Partida[];
  onEditar: (partida: Partida) => void;
  isAdmin: boolean;
};

export default function TabelaPartidas({ partidas, onEditar, isAdmin }: Props) {
  return (
    <div className="mt-8 overflow-x-auto rounded-lg shadow">
      <h2 className="text-2xl font-semibold mb-4">Histórico de Partidas</h2>
      <table className="min-w-full table-auto bg-white text-sm text-gray-800">
        <thead>
          <tr className="bg-gray-100 text-left font-semibold">
            <th className="px-4 py-2">Data</th>
            <th className="px-4 py-2">Time 1</th>
            <th className="px-4 py-2">Gols</th>
            <th className="px-4 py-2">Gols</th>
            <th className="px-4 py-2">Time 2</th>
            {isAdmin && <th className="px-4 py-2">Ações</th>}
          </tr>
        </thead>
        <tbody>
          {partidas.map((partida) => (
            <tr key={partida.id} className="border-t">
              <td className="px-4 py-2">{partida.data}</td>
              <td className="px-4 py-2">{partida.time1}</td>
              <td className="px-4 py-2 text-center">{partida.golsTime1}</td>
              <td className="px-4 py-2 text-center">{partida.golsTime2}</td>
              <td className="px-4 py-2">{partida.time2}</td>
              {isAdmin && (
                <td className="px-4 py-2">
                  <Button size="small" onClick={() => onEditar(partida)}>
                    Editar
                  </Button>
                </td>
              )}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
