// src/app/components/ui/Dialog.tsx

type DialogProps = {
  open: boolean;
  onOpenChange: (isOpen: boolean) => void;
  children: React.ReactNode;
};

// Componente principal do Dialog
export default function Dialog({ open, onOpenChange, children }: DialogProps) {
  return (
    <div
      className={`fixed inset-0 bg-black bg-opacity-50 ${open ? 'block' : 'hidden'}`}
      onClick={() => onOpenChange(false)}  // Fecha o modal ao clicar fora
    >
      <div
        className="relative mx-auto my-20 p-6 bg-white rounded shadow-lg max-w-lg"
        onClick={(e) => e.stopPropagation()}  // Impede o fechamento ao clicar dentro do modal
      >
        {children}
      </div>
    </div>
  );
}

// Componente DialogContent
type DialogContentProps = {
  children: React.ReactNode;
};

export function DialogContent({ children }: DialogContentProps) {
  return <div className="p-4">{children}</div>; // Estilo básico do conteúdo do modal
}

// Componente DialogHeader
type DialogHeaderProps = {
  children: React.ReactNode;
};

export function DialogHeader({ children }: DialogHeaderProps) {
  return <div className="text-lg font-semibold mb-4">{children}</div>; // Título do modal
}

// Componente DialogTitle
type DialogTitleProps = {
  children: React.ReactNode;
};

export function DialogTitle({ children }: DialogTitleProps) {
  return <h2 className="text-xl font-bold">{children}</h2>; // Título dentro do modal
}
