'use client';

import { Partida } from '@/app/types';
import Dialog, { DialogHeader, DialogTitle, DialogContent } from '@/app/components/ui/Dialog';  // Importação padrão sem as chaves
import Input from '@/app/components/ui/Input';
import Button from '@/app/components/ui/Button';
import { useState, useEffect } from 'react';

type Props = {
  isOpen: boolean;
  onClose: () => void;
  onSalvar: (partida: Partida) => void;
  partidaEditando?: Partida | null;
};

export default function ModalPartida({ isOpen, onClose, onSalvar, partidaEditando }: Props) {
  const [partida, setPartida] = useState<Partida>({
    id: '',
    time1: '',
    time2: '',
    golsTime1: 0,
    golsTime2: 0,
    data: '',
  });

  useEffect(() => {
    if (partidaEditando) {
      setPartida(partidaEditando);
    } else {
      setPartida({
        id: '',
        time1: '',
        time2: '',
        golsTime1: 0,
        golsTime2: 0,
        data: '',
      });
    }
  }, [partidaEditando]);

  const handleSubmit = () => {
    if (!partida.time1 || !partida.time2 || !partida.data) return;
    onSalvar(partida);
    onClose();
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>{partidaEditando ? 'Editar Partida' : 'Adicionar Partida'}</DialogTitle>
        </DialogHeader>

        <div className="grid gap-4">
          <Input
            placeholder="Time 1"
            value={partida.time1}
            onChange={(e) => setPartida({ ...partida, time1: e.target.value })}
          />
          <Input
            placeholder="Gols Time 1"
            type="number"
            value={partida.golsTime1}
            onChange={(e) => setPartida({ ...partida, golsTime1: +e.target.value })}
          />
          <Input
            placeholder="Gols Time 2"
            type="number"
            value={partida.golsTime2}
            onChange={(e) => setPartida({ ...partida, golsTime2: +e.target.value })}
          />
          <Input
            placeholder="Time 2"
            value={partida.time2}
            onChange={(e) => setPartida({ ...partida, time2: e.target.value })}
          />
          <Input
            type="date"
            value={partida.data}
            onChange={(e) => setPartida({ ...partida, data: e.target.value })}
          />
          <Button onClick={handleSubmit}>
            {partidaEditando ? 'Salvar Alterações' : 'Adicionar'}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
