'use client';

import { Jogador } from '@/app/types';
import Dialog, { DialogHeader, DialogTitle, DialogContent } from '@/app/components/ui/Dialog';  // Importação padrão sem as chaves
import Input from '@/app/components/ui/Input';
import Button from '@/app/components/ui/Button';
import { useState, useEffect } from 'react';

type Props = {
  isOpen: boolean;
  onClose: () => void;
  onSalvar: (jogador: Jogador) => void;
  jogadorEditando?: Jogador | null;
};

export default function ModalJogador({ isOpen, onClose, onSalvar, jogadorEditando }: Props) {
  const [jogador, setJogador] = useState<Jogador>({
    id: '',
    nome: '',
    pontos: 0,
    saldoGols: 0,
    vitorias: 0,
    derrotas: 0,
  });

  useEffect(() => {
    if (jogadorEditando) {
      setJogador(jogadorEditando);
    } else {
      setJogador({
        id: '',
        nome: '',
        pontos: 0,
        saldoGols: 0,
        vitorias: 0,
        derrotas: 0,
      });
    }
  }, [jogadorEditando]);

  const handleSubmit = () => {
    if (!jogador.nome.trim()) return;
    onSalvar(jogador);
    onClose();
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>{jogadorEditando ? 'Editar Jogador' : 'Adicionar Jogador'}</DialogTitle>
        </DialogHeader>

        <div className="grid gap-4">
          <Input
            placeholder="Nome"
            value={jogador.nome}
            onChange={(e) => setJogador({ ...jogador, nome: e.target.value })}
          />
          <Input
            type="number"
            placeholder="Pontos"
            value={jogador.pontos}
            onChange={(e) => setJogador({ ...jogador, pontos: +e.target.value })}
          />
          <Input
            type="number"
            placeholder="Saldo de Gols"
            value={jogador.saldoGols}
            onChange={(e) => setJogador({ ...jogador, saldoGols: +e.target.value })}
          />
          <Input
            type="number"
            placeholder="Vitórias"
            value={jogador.vitorias}
            onChange={(e) => setJogador({ ...jogador, vitorias: +e.target.value })}
          />
          <Input
            type="number"
            placeholder="Derrotas"
            value={jogador.derrotas}
            onChange={(e) => setJogador({ ...jogador, derrotas: +e.target.value })}
          />
          <Button onClick={handleSubmit}>
            {jogadorEditando ? 'Salvar Alterações' : 'Adicionar'}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
