// src/app/components/Header.tsx

'use client';

import Button from '../components/ui/Button'; // Correto
import { Jogador } from '@/app/types'; // Adicionando a importação do tipo Jogador

type Props = {
  ordenarPor: (campo: keyof Jogador) => void; // Agora, 'Jogador' está disponível
  isAdmin: boolean;
  abrirModalJogador: () => void;
  abrirModalPartida: () => void;
};

export default function Header({
  ordenarPor,
  isAdmin,
  abrirModalJogador,
  abrirModalPartida,
}: Props) {
  return (
    <div className="flex flex-col md:flex-row justify-between items-center mb-6 gap-4">
      <h1 className="text-3xl font-bold text-center">Ranking da Pelada</h1>
      
      <div className="flex flex-wrap justify-center gap-2">
        <Button onClick={() => ordenarPor('pontos')}>Ordenar por Pontos</Button>
        <Button onClick={() => ordenarPor('saldoGols')}>Saldo de Gols</Button>
        <Button onClick={() => ordenarPor('vitorias')}>Vitórias</Button>
        <Button onClick={() => ordenarPor('derrotas')}>Derrotas</Button>
      </div>

      {isAdmin && (
        <div className="flex flex-wrap justify-center gap-2">
          <Button onClick={abrirModalJogador}>Adicionar Jogador</Button>
          <Button onClick={abrirModalPartida}>Adicionar Partida</Button>
        </div>
      )}
    </div>
  );
}
