// src/components/ui/Button.tsx

type ButtonProps = {
  onClick: () => void;
  children: React.ReactNode;
  size?: 'small' | 'medium' | 'large'; // Adicionando a propriedade `size` opcional
};

export default function Button({ onClick, children, size = 'medium' }: ButtonProps) {
  // A lógica para ajustar o estilo com base no `size`
  let buttonSizeClasses = '';

  switch (size) {
    case 'small':
      buttonSizeClasses = 'px-2 py-1 text-sm';
      break;
    case 'medium':
      buttonSizeClasses = 'px-4 py-2';
      break;
    case 'large':
      buttonSizeClasses = 'px-6 py-3 text-lg';
      break;
    default:
      buttonSizeClasses = 'px-4 py-2';
      break;
  }

  return (
    <button
      onClick={onClick}
      className={`bg-blue-500 text-white rounded hover:bg-blue-600 transition ${buttonSizeClasses}`}
    >
      {children}
    </button>
  );
}
